  .titleinfo {
    font-size: 2rem;
    line-height: 2rem;
    margin: 0;
    padding: 0;
    font-weight: 600;
  }
  .contentinfo {
    color: rgba(0,0,0,0.5);
    font-size: 18px;
    line-height: 24px;
    max-width: 80%;
    margin: 1.25rem auto 0 auto;
  }
//   body {
//     background: #f2f2f2;
//   }
  @-webkit-keyframes plopp {
    0% {
      transform: translate(0, 0);
      opacity: 1;
    }
    100% {
      transform: translate(0, -10px);
      opacity: 0;
    }
  }
  @keyframes plopp {
    0% {
      transform: translate(0, 0);
      opacity: 1;
